import React, {useState, useContext} from 'react';
import {Seo, Stars} from '.' 
import priceFormat from '../utils/priceFormat'
import { Tag, StyledProductDetail, QtySelect, SizeSelect, SizeButton, Button } from '../styles/components'
import {CartContext} from '../context'


export default function ProductDetail({ pageContext: { unit_amount, id, product: { name, metadata } } }){
  const formatPrice = priceFormat(unit_amount)
  const [size, setSize] = useState(2)
  const[qty, setQty] = useState(1)
  const {addToCart} = useContext(CartContext)
  const handleSubmit = () => {
    addToCart({unit_amount, formatPrice, id, name, metadata, quantity: qty})
  }
  return (
    
    <StyledProductDetail>
        <Seo title={name}/>
        <img src={metadata.img} alt={name}/> 
        <div>
            <Tag>Mundial de Qatar</Tag>
            <h2>{name}</h2>
            <b> U$D {formatPrice}</b>
            <Stars/>
            {metadata.color && <h3>Color: {metadata.color}</h3>}
            <small>{metadata.description}</small>
            {metadata.wear &&(
              <SizeSelect selected={size}>
                <SizeButton onClick={() => setSize(1)}>S</SizeButton>
                <SizeButton onClick={() => setSize(2)}>M</SizeButton>
                <SizeButton onClick={() => setSize(3)}>L</SizeButton>
                <SizeButton onClick={() => setSize(4)}>XL</SizeButton>
              </SizeSelect>
            )}
            <p>Cantidad:</p>
            <QtySelect>
              <button onClick={() => (qty > 1 ? setQty(qty - 1): null)}>-</button>
              <input type="text" disabled value={qty}></input>
              <button onClick={() => (setQty(qty + 1))}>+</button>
            </QtySelect>
            <Button onClick={handleSubmit}>Agregar al carrito</Button>
        </div>
    </StyledProductDetail>

  )
}
